.nav-bar{
  box-shadow: 0px 3px 7.5px rgba(0, 0, 0, 0.1);
  padding: 20px 36px;
  height: 70px;
  display: flex;
}
.nav-logo{
  max-width: 100px;
}
.hero-section{
  padding: 20px;
  display: flex;
  flex-direction: column;
}
.hero-img{
  max-width: 400px;
  align-self: center;
}
.hero-text{
  font-size: 36px;
  margin-bottom: 16px;
  font-weight: 600;
  margin-bottom: 16px;
}
.hero-p{
  font-size: 16px;
  margin-top: 0;
  font-weight: 300;
}
.cards-list{
  display: flex;
  flex-wrap: nowrap;
  gap: 20px;
  overflow-x: auto;
  padding: 20px;
}
.card{
  width: 175px;
  flex: 0 0 auto;
  display: flex;
  flex-direction: column;
  font-size: 12px;
  font-weight: 300;
  position: relative;
}

.star{
  height: 14px;
  margin-top: 3px;
}
.stats{
  display: flex;
  align-items: center;
}
.count{
  color: #918E9B;
}
.title{
  overflow: hidden;
  text-overflow: ellipsis;
}
.price{
  margin-top: auto;
}
.pics{
  width: 100%;
  border-radius: 9px;
  border-bottom: 9px;
}
.badge{
    position: absolute;
    top: 6px;
    left: 6px;
    background-color: white;
    padding: 5px 7px;
    border-radius: 2px;
    font-weight: bold;
}